.side {
  background-color: #f3f3f3;
  height: 100vh;
  width: 250px;

  img {
    padding: 20px;
    width: 200px;
    height: 200px;
  }

  ul {
    margin-left: 0;
    padding-left: 0;

    li {
      padding: 10px;
      list-style: none;

      &:hover {
        background-color: #b6b6b6;
      }

      a {
        display: block;
      }
    }
  }
}